import Vue from 'vue'
import Router from 'vue-router'
import Landing from '@/components/Landing'
import Dashboard from '@/components/Dashboard'
import EditAct from '@/components/EditAct'
import NewAct from '@/components/NewAct'
import ViewAct from '@/components/ViewAct'
import ListActs from '@/components/ListActs'
import Login from '@/components/Login'
import Register from '@/components/Register'
import firebase from 'firebase/app'
import 'firebase/auth'
import db from '../lib/firebaseInit'

Vue.use( Router )

let router = new Router( {
  routes: [ 
    {
      path: '/'
      , name: 'landing'
      , component: Landing
      , meta: {
        requiresAuth: true
      }
    },
    {
      path: '/dashboard'
      , name: 'dashboard'
      , component: Dashboard
      , meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login'
      , name: 'login'
      , component: Login
      , meta: {
        requiresGuest: true
      }
    },
    {
      path: '/register'
      , name: 'register'
      , component: Register
      , meta: {
        requiresGuest: true
      }
    },
    {
      path: '/acts/new'
      , name: 'new-act'
      , component: NewAct
      , meta: {
        requiresAuth: true
      }
    },
    {
      path: '/acts/list'
      , name: 'list-acts'
      , component: ListActs
      , meta: {
        requiresAdmin: true
      }
    },
    {
      path: '/acts/edit/:act_id'
      , name: 'edit-act'
      , component: EditAct
      , meta: {
        requiresAuth: true
      }
    },
    {
      path: '/acts/:act_id'
      , name: 'view-act'
      , component: ViewAct
      , meta: {
        requiresAuth: true
      }
    }
  ]
} )

router.beforeEach((to, from, next) => {
  if( to.matched.some( record => record.meta.requiresAuth ) ) {
    if(!firebase.auth().currentUser) next( { 
      path:'/login' 
      , query: {
        redirect: to.fullPath
      }
    } )

    else next()
  } else if( to.matched.some( record => record.meta.requiresGuest ) ) {
    if(firebase.auth().currentUser) next( { 
      path:'/' 
      , query: {
        redirect: to.fullPath
      }
    } )
    else next()
  } else if( to.matched.some( record => record.meta.requiresAdmin ) ) {
    var userEmail = firebase.auth().currentUser.email
    db.collection('users')
    .where( 'email', '==', userEmail )
    .get()
    .then( snapshot => {
      snapshot.forEach( doc => {
        var data = doc.data()
        if( data.access != 'admin' ) next( {
            path:'/'
            , query: {
              redirect: to.fullPath
            }
          } )
        else next()
      } )
    } )
  } else {
    next()
  }
})

export default router
