<template>
  <div id="view-act" class="row justify-content-md-center mt-5">
    <div class="col-sm-6">
      <div class="card pt-0">
        <h3 class="card-header">{{ act_name }}
          <div class="card-text">
            <div class="badge badge-secondary mr-1">
              <i class="fas fa-map-pin"></i>
              <span> Locatated: </span>
              {{ origin }}
            </div>
            <div class="badge badge-secondary mr-1">
              <i class="fas fa-users"></i>
              {{ noMembers }}
            </div>
            <div class="badge badge-secondary">
              <i class="fas fa-microphone"></i>
              {{ genre }}
            </div>
          </div>
        </h3>
        <div v-if="gallery" id="galleryCarousel" class="carousel slide" data-ride="carousel">
          <div class="carousel-inner">
            <div v-for="image in gallery" class="carousel-item active">
              <img 
                class="d-block w-100" 
                :src="'https://firebasestorage.googleapis.com/v0/b/fat-tuesday.appspot.com/o/uploads-output%2Fresized-large-' + image + '?alt=media'" 
                alt="First slide">
            </div>
          </div>
        </div>
        <div class="card-body">
          <h5 class="first">Contact Name</h5>
          <div class="card-text">{{ contact_name }}</div>
          <h5 class="card-title">Contact Telephone</h5>
          <div class="card-text">{{ contact_tel }}</div>
          <h5 class="card-title">Contact Email</h5>
          <div class="card-text">{{ contact_email }}</div>
          <h5 class="card-title">Biography</h5>
          <div class="card-text">{{ bio }}</div>
          <h5 class="card-title">Tell us about your most recent gigs and any festival appearances in 2018</h5>
          <div class="card-text">{{ recentGigs }}</div>
          <h5 class="card-title">Dates that you are available</h5>
          <ul class="list-group">
            <li class="list-group-item list-group-item-dark" v-for="dateAvailable in datesAvailable"><small>{{ dateAvailable }}</small></li>
          </ul>
          <div v-if="musicLink" class="">
            <h5 class="card-title">
              Music Link SoundCloud, BandCamp etc...
            </h5>
            <a :href="musicLink">
              {{ musicLink }}
            </a>
          </div>
          <div v-if="spotifyLink" class="">
            <h5 class="">Spotify Link</h5>
            <a :href="spotifyLink">
              {{spotifyLink}}
            </a>
          </div>
          <div v-if="webLink" class="">
            <h5 class="">Web address or Facebook Page</h5>
            <a :href="webLink">
              {{webLink}}
            </a>
          </div>
          <div v-if="twitterLink" class="">
            <h5 class="">Twitter Link</h5>
            <a :href="twitterLink">
              {{twitterLink}}
            </a>
          </div>
          <div v-if="videoLink" class="">
            <h5 class="">YouTube / Vimeo Link</h5>
            <a :href="videoLink">
              {{videoLink}}
            </a>
          </div>
        </div>
        <div class="card-footer">
          <div v-if="createdAt" class="card-muted">Created: {{ createdAt }}</div>
          <div v-if="updatedAt != ''" class="card-muted">Last updated: {{ updatedAt }}</div>
          
        </div>
      </div>
      <div class="mt-3">
        <router-link to="/" class="btn btn-lg btn-block btn-secondary">Back</router-link>
        <router-link v-bind:to="{ name:'edit-act', params: act_id }" class="btn btn-lg btn-block btn-outline-secondary">Edit</router-link>
        <button v-on:click="deleteAct" class="btn btn-lg btn-block btn-danger">Delete</button>
      </div>
  </div>
</div>
</template>
<script>
import firebase from 'firebase/app'
import db from '../lib/firebaseInit'
export default {
  name:'view-act'
  , data() {
    return {
      act_id: ''
      , contact_name: ''
      , contact_tel: ''
      , contact_email: ''
      , act_name: ''
      , origin: ''
      , noMembers: ''
      , genre: ''
      , recentGigs: ''
      , gallery: ''
      , bio: ''
      , datesAvailable: []
      , musicLink: ''
      , spotifyLink: ''
      , webLink: ''
      , twitterLink: ''
      , videoLink: ''
      , createdAt: ''
      , updatedAt: ''
    }
  },
  beforeRouteEnter( to, from, next ) {
    db.collection('acts')
    .doc( to.params.act_id )
    .onSnapshot( doc => {
      const data = doc.data()
      const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
      let updatedLocal = ''
      if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
      next( vm => {
        vm.contact_name = data.contact_name
        vm.contact_email = data.contact_email
        vm.contact_email = data.contact_email
        vm.act_name = data.act_name
        vm.origin = data.origin
        vm.noMembers = data.noMembers
        vm.genre = data.genre 
        vm.recentGigs = data.recentGigs 
        vm.gallery = data.gallery
        vm.bio = data.bio 
        vm.datesAvailable = data.datesAvailable 
        vm.musicLink = data.musicLink 
        vm.spotifyLink = data.spotifyLink 
        vm.webLink = data.webLink 
        vm.twitterLink = data.twitterLink 
        vm.videoLink = data.videoLink 
        vm.createdAt = createdLocal
        vm.updatedAt = updatedLocal
      } )
    } )
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('acts')
      .doc( this.$route.params.act_id )
      .onSnapshot( doc => {
        const data = doc.data()
        const createdLocal = new Date( data.createdAt ).toLocaleString("en-GB") 
        let updatedLocal = ''
        if( data.updatedAt ) updatedLocal = new Date( data.updatedAt ).toLocaleString("en-GB") 
        else updatedLocal = ''
        this.contact_name = data.contact_name
        this.contact_tel = data.contact_tel
        this.contact_email = data.contact_email
        this.act_name = data.act_name
        this.origin = data.origin
        this.noMembers = data.noMembers
        this.genre = data.genre 
        this.recentGigs = data.recentGigs 
        this.gallery = data.gallery 
        this.bio = data.bio 
        this.datesAvailable = data.datesAvailable 
        this.musicLink = data.musicLink 
        this.spotifyLink = data.spotifyLink 
        this.webLink = data.webLink 
        this.twitterLink = data.twitterLink 
        this.videoLink = data.videoLink 
        this.createdAt = createdLocal
        this.updatedAt = updatedLocal
      } )
    },
    deleteAct() {
      if( confirm( 'Are you sure' ) ) {
        db.collection('acts')
        .doc( this.$route.params.act_id )
        .onSnapshot( doc => {
          doc.ref.delete()
          this.$router.push('/acts/list')
        } )
      }
    }
  }
}
</script>
<style scoped>
h5 {
  margin-top:2rem;
  padding-top:1rem;
  border-top: thin #eee solid;
}
.first {
  margin-top:0;
  padding-top:0;
  border-top:none
}
.list-group {
  margin-bottom:2rem
}
</style>
